import { Button, Dialog, Select } from '@/components/ui'
import { Coins, createPaymentLink, Memberships } from '../methods'
import { useState } from 'react'
import { SiCashapp } from 'react-icons/si'
import { Periods } from '../membership'
import { initMercadoPago, Payment } from '@mercadopago/sdk-react'
import userSlice from '@/store/slices/auth/userSlice'
import { useAppSelector } from '@/store'
import { OPTIONS } from '@/utils/packs'
import { error } from 'console'
import PaymentStatus from './PaymentStatus'
import SantanderMethod from './SantanderMethod'

export type MpProps = {
  formData: {
    installments: number
    issuer_id: number
    payer: {
      email: string
    }
    payment_method_id: string
    token: string
    transaction_amount: number
  }
  paymentType: string
  selectedPaymentMethod: string
}

const customization = {
  paymentMethods: {
    atm: 'all',
    ticket: 'all',
    creditCard: 'all',
    debitCard: 'all',
    mercadoPago: ['visa', 'mastercard', 'amex'],
  },
}

const GenerateQR = ({
  type,
  loading,

  options,
  founder,
}: {
  type: Memberships
  loading: boolean
  options: { value: Periods; label: string }[]
  founder?: boolean
}) => {
  const user = useAppSelector((state) => state.auth.user)
  const [period, setPeriod] = useState<Periods>('monthly')
  const [showCoin, setShowCoin] = useState(false)
  const [showBrick, setShowBrick] = useState(false)
  const [disabled, setDisabled] = useState(false)
  const [showSantander, setShowSantander] = useState(false)
  const [paymentId, setPaymentId] = useState<string | null>(null)
  initMercadoPago(import.meta.env.VITE_MP_PK)

  const _create = (coin: Coins) => {
    try {
      setDisabled(true)
      if (coin == 'MXN') {
        setShowBrick(true)
      } else if (coin == 'USDT') {
        createPaymentLink(user.uid!, type, coin)
      }
    } catch (err) {
      console.error(err)
    } finally {
      setDisabled
    }
  }

  const _createSantander = (coin: 'MXN') => {
    try {
      setDisabled(true)
      setShowSantander(true)
    } catch (err) {
      console.error(err)
    } finally {
      setDisabled
    }
  }

  const selectCoin = () => {
    setShowCoin(true)
  }

  const pricing = () => {
    const membership = OPTIONS.find((option) => option.value === type)
    if (!membership)
      return {
        amount: 169 * 18,
      }
    return {
      amount: membership?.price * 18,
    }
  }

  const onSubmit = (
    formData: MpProps,
    userId: string,
    type: Memberships,
    coin: Coins
  ) => {
    return new Promise((resolve, reject) => {
      createPaymentLink(userId, type, coin, formData)
        .then((response) => response?.json())
        .then((response) => {
          console.log('Datos del pago:', response)
          setPaymentId(response.id)
          resolve(response)
        })
        .catch((error) => {
          reject()
        })
    })
  }

  const onError = async (error: any) => {
    console.log(error)
  }

  const onReady = async () => {
    // console.log("aqui esta ready")
  }

  if (showCoin) {
    return (
      <div className="flex flex-col  space-y-2">
        {showBrick && user && (
          <Dialog isOpen={showBrick} onClose={() => setShowBrick(false)}>
            {paymentId ? (
              <PaymentStatus paymentId={paymentId} />
            ) : (
              <Payment
                initialization={pricing()}
                onSubmit={(formData: any) =>
                  onSubmit(formData, user.uid as string, type, 'MXN')
                }
                onReady={onReady}
                onError={onError}
                customization={customization}
              />
            )}
          </Dialog>
        )}

        {showSantander && (
          <Dialog
            isOpen={showSantander}
            onClose={() => setShowSantander(false)}
          >
            <SantanderMethod
              onclose={() => setShowSantander(false)}
              type={type}
              coin="MXN"
              user={user}
            />
          </Dialog>
        )}
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
          {import.meta.env.VITE_ENABLE_MP && (
            <Button
              className="h-max"
              disabled={disabled}
              onClick={() => _create('MXN')}
            >
              <div className="flex flex-col items-center space-y-4">
                <img
                  src="/img/mp.png"
                  height={50}
                  width={50}
                  className="h-[50px] w-[50px]"
                />
                <span>Mercado Pago (MXN)</span>
              </div>
            </Button>
          )}
          {import.meta.env.VITE_ENABLE_SANTANDER &&
            import.meta.env.VITE_ENABLE_SANTANDER == 1 && (
              <Button
                className="h-max"
                disabled={disabled}
                onClick={() => _createSantander('MXN')}
              >
                <div className="flex flex-col items-center space-y-4">
                  <img
                    src="/img/santander.png"
                    height={50}
                    width={50}
                    className="h-[50px] w-[50px]"
                  />
                  <span>Santander (MXN)</span>
                </div>
              </Button>
            )}
          {import.meta.env.VITE_ENABLE_COINPAYMENTS && (
            <Button
              className="h-max"
              disabled={disabled}
              onClick={() => _create('USDT')}
            >
              <div className="flex flex-col items-center space-y-4">
                <img
                  src="/img/usdt-logo.png"
                  height={50}
                  width={50}
                  className="h-[50px] w-[50px]"
                />
                <span>Tether (USDT)</span>
              </div>
            </Button>
          )}

          <Button
            className="h-max"
            disabled={disabled}
            onClick={() => _create('USDT')}
          >
            <div className="flex flex-col items-center space-y-4">
              <img
                src="/img/balance1.webp"
                height={50}
                width={50}
                className="h-[50px] w-[50px]"
              />
              <span>Balance (USD)</span>
            </div>
          </Button>
          <Button
            className="h-max"
            disabled={disabled}
            onClick={() => _create('USDT')}
          >
            <div className="flex flex-col items-center space-y-4">
              <img
                src="/img/monedero.png"
                height={50}
                width={50}
                className="h-[50px] w-[50px]"
              />
              <span>Monedero E. (USD)</span>
            </div>
          </Button>
        </div>
      </div>
    )
  }

  return (
    <div>
      {showBrick ? (
        // <Payment
        //   initialization={initialization}
        //   onSubmit={onSubmit}
        //   onReady={onReady}
        //   onError={onError}
        //   customization={customization}
        // />
        <div></div>
      ) : (
        <Button loading={loading} onClick={selectCoin}>
          Ir a pagar
        </Button>
      )}
    </div>
  )
}

export default GenerateQR
